import styled from '@emotion/styled';
import Popper from '@material-ui/core/Popper';
import React, { FC, useState, useContext } from 'react';
import { useTranslation } from 'react-i18next';

import { tagsContext } from '../../../state/tags.context';
import IconArrowBack from '../../../ui/icons/IconArrowBack';
import { TagField } from '../../tags/interfaces/tag.interface';

const TagInfoBlock = styled('div')`
  background-color: var(--FFFFFF-profile-table-show-all-tags-button);
  box-shadow: 0 1px 4px var(--00000003-profile-table-show-all-tags-button), 0 4px 8px var(--00000005-profile-table-show-all-tags-button), 0 1px 12px var(--0000001F-profile-table-show-all-tags-button);
  border-radius: 4px;
  padding: 8px 12px 7px;
  position: relative;
  top: -3px;
`;

const TagInfo = styled('div')`
  font-family: 'Roboto';
  font-style: normal;
  letter-spacing: 0.25px;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: var(--767676-profile-table-show-all-tags-button);
`;

const TagsBackBtnIcon = styled('div')`
  display: inline-block;
  position: relative;
  top: 2px;
  margin-right: 10px;
`;

const TagsBackBtn = styled('div')`
  display: inline-block;
  margin-left: 16px;
  color: var(--000000D9-profile-table-show-all-tags-button);
  cursor: pointer;
  font-weight: 400;

  &:hover {
    color: var(--00A987-profile-table-show-all-tags-button);
  }
`;

interface IShowAllTagsBtn {
  field: TagField;
}

const ShowAllTagsBtn: FC<IShowAllTagsBtn> = (props) => {
  const { field } = props;

  const [anchorInfoEl, setAnchorInfoEl] = useState<null | HTMLElement>(null);

  const { selectedTag, setSelectedTag } = useContext(tagsContext);

  const { t: translation } = useTranslation();

  const infoOpen = Boolean(anchorInfoEl);
  const showAllKey = field === 'custom-status' ? 'tags.showAllStatuses' : 'tags.showAllTags';

  const isShowAllTagsButtonShown = field === selectedTag?.field;

  const handleMouseEnter = async (event: React.MouseEvent<HTMLElement, MouseEvent>): Promise<void> => {
    setAnchorInfoEl(event.currentTarget);
  };

  const handleMouseLeave = (): void => {
    setAnchorInfoEl(null);
  };

  if (!isShowAllTagsButtonShown) {
    return null;
  }

  return (
    <TagsBackBtn
      onClick={(): void => setSelectedTag(null)}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      <TagsBackBtnIcon>
        <IconArrowBack />
      </TagsBackBtnIcon>
      <Popper
        open={infoOpen}
        anchorEl={anchorInfoEl}
        style={{ zIndex: 11000 }}
        placement='top'
      >
        <TagInfoBlock>
          <TagInfo>
            {translation(showAllKey)}
          </TagInfo>
        </TagInfoBlock>
      </Popper>
    </TagsBackBtn>
  );
};

export default ShowAllTagsBtn;
