import React, { FC } from 'react';

import ProxyItemCheckbox from './proxy-item-checkbox';
import { ProxyItemWrapperEl, ProxyTitleBlockEl, ProxyTitleWrapperEl } from './styles/proxy-item';
import { ProxyFlagBlockEl } from './styles/proxy-list-item';
import { NEW_FEATURES } from '../../../../../../feature-toggle';
import { useProxyManagerState } from '../../../../state/proxy-manager-modal-status.atom';
import { selectProfileProxy } from '../../../../state/proxy-operations.commands';
import { useSharedProxyInfo } from '../../../../state/shared-proxy-info.atom';
import { saveProxy } from '../../api';
import CheckProxyBtn from '../../components/check-proxy-btn';
import { IProxy } from '../../components/interfaces/IProxy';
import { ISharedProxyInfo } from '../../components/interfaces/ISharedProxyInfo';
import ProxyFlag from '../../components/proxy-flag';
import { getProxyTitle } from '../../proxy-helpers';

interface IProps {
  proxy?: IProxy;
  currentProxy?: IProxy;
  profileId?: string;
  hideDot?: boolean;
  isSharedProxy?: boolean;
  showCheckbox?: boolean;
  localProxyElementLocation: string | null;
}

const ProxyItem: FC<IProps> = (props) => {
  const {
    proxy,
    currentProxy,
    profileId,
    hideDot,
    isSharedProxy,
    showCheckbox,
    localProxyElementLocation,
  } = props;

  const title = getProxyTitle(proxy);

  const sharedProxyInfos = useSharedProxyInfo();

  const { currentProfileId } = useProxyManagerState();

  const saveProfileProxyStatus = async (): Promise<IProxy | undefined> => {
    const isFromSharedProfile = (proxy && !proxy._id && proxy.id);

    if (!proxy || proxy._id || isFromSharedProfile) {
      return;
    }

    const proxyRes = await saveProxy({
      proxyItem: proxy,
      checkProxy: false,
    }, currentProfileId || '');

    selectProfileProxy(profileId, proxyRes);

    return proxyRes;
  };

  if (!proxy) {
    return null;
  }

  const isFailed = (proxy?.status === false);
  const titleRes = title || '-';

  let sharedProxyInfo: ISharedProxyInfo | undefined;
  let { country } = proxy;
  let checkProxy = proxy;
  if (isSharedProxy) {
    sharedProxyInfo = sharedProxyInfos.find((el) => el.profileId === profileId);
    if (sharedProxyInfo) {
      ({ country } = sharedProxyInfo);
      checkProxy = {
        ...proxy,
        ...sharedProxyInfo,
      };
    }
  }

  const renderStatus = (): JSX.Element => {
    if (showCheckbox) {
      return <ProxyItemCheckbox proxy={proxy} />;
    }

    return (
      <CheckProxyBtn
        proxy={checkProxy}
        currentProxy={currentProxy}
        profileId={profileId}
        hideDot={hideDot}
        saveProfileProxyStatus={saveProfileProxyStatus}
        isSharedProxy={isSharedProxy}
        showTooltipCheckProxy={true}
        localProxyElementLocation={localProxyElementLocation}
      />
    );
  };

  return (
    <ProxyItemWrapperEl>
      <ProxyFlagBlockEl newStyle={!!NEW_FEATURES.header} isFailed={isFailed} className='proxy-flag-block'>
        {renderStatus()}
        <ProxyFlag region={country} />
      </ProxyFlagBlockEl>
      <ProxyTitleWrapperEl newStyle={!!NEW_FEATURES.header}>
        <ProxyTitleBlockEl className='proxy-title' isFailed={isFailed}>
          <div className='nowrap'>
            {titleRes}
          </div>
        </ProxyTitleBlockEl>
      </ProxyTitleWrapperEl>
    </ProxyItemWrapperEl>
  );
};

export default ProxyItem;
