import Popper from '@material-ui/core/Popper';
import React, { FC, memo, useCallback, useEffect } from 'react';

import ProxyList from './proxy-list';
import ProxyManagerEditView from './proxy-manager-edit-view';
import { ProxyManagerContainer, ProxyManagerContent, ProxyPopper } from './styles';
import { NEW_FEATURES } from '../../../../../feature-toggle';
import { useProfilesList } from '../../../state/profiles-list.atom';
import { closeProxyManager, useProxyManagerState } from '../../../state/proxy-manager-modal-status.atom';
import { useCurrentTheme } from '../../../state/theme.atom';
import getSelectedThemeColor from '../../../utils/get-selected-theme-color';
import RemovingConfirm from '../components/removing-confirm';
import { EMPTY_PROXY } from '../proxy-helpers';

const ProxyManager: FC = () => {
  const { modalView, containerElement, currentProfileId } = useProxyManagerState();
  const profilesList = useProfilesList();
  const currentProfile = profilesList.find(profile => profile.id === currentProfileId);
  const currentProxy = currentProfile?.proxy || EMPTY_PROXY;
  const theme = useCurrentTheme();
  const isDarkTheme = getSelectedThemeColor(theme) === 'dark';

  const handleGlobalKeyDown = useCallback((event: KeyboardEvent): void => {
    if (event.key === 'Escape') {
      closeProxyManager();
    }
  }, []);

  useEffect(() => {
    document.addEventListener('keydown', handleGlobalKeyDown, true);

    return () => {
      document.removeEventListener('keydown', handleGlobalKeyDown, true);
    };
  }, []);

  return (
    <Popper
      open={!!containerElement}
      anchorEl={containerElement}
      style={{
        zIndex: 1060,
        margin: '0 16px 0 -16px',
      }}
      disablePortal={false}
      placement='bottom-start'
      modifiers={{
        preventOverflow: {
          enabled: true,
          boundariesElement: 'window',
        },
        offset: {
          enabled: true,
        },
      }}
    >
      <ProxyPopper className='proxy-popper'>
        <ProxyManagerContainer className='proxy-popper-container' isDarkTheme={isDarkTheme}>
          <ProxyManagerContent newStyle={!!NEW_FEATURES.header}>
            {modalView === 'proxy-list' ? (
              <ProxyList
                currentProxy={currentProxy}
                localProxyElementLocation='no-location'
              />
            ) : (
              <ProxyManagerEditView
                currentProxy={currentProxy}
                localProxyElementLocation='no-location'
              />
            )}
            <RemovingConfirm />
          </ProxyManagerContent>
        </ProxyManagerContainer>
      </ProxyPopper>
    </Popper>
  );
};

export default memo(ProxyManager);
