import React, { FC, useContext } from 'react';

import { NameColText } from './styles';
import { NEW_FEATURES } from '../../../../../feature-toggle';
import { ITemplatesCtx, tagsContext, templatesContext } from '../../../state';
import { SorterCol } from '../../../ui/gologin-table/sorter-col';
import { ProfilesTableColumn } from '../components/interfaces/app-settings.interface';
import ShowAllTagsBtn from '../components/show-all-tags-btn';
import ButtonQuickCreateProfile from '../quick-create-profile';
import { ContainerNameCol } from '../table/styles';

interface IColumnHeader {
  column: ProfilesTableColumn;
  title: string;
  enableDrag?: boolean;
  hasSort?: boolean;
  sorterField?: string;
  quickCreateProfile?: (templateCtx: ITemplatesCtx) => Promise<void>;
}

const ColumnHeader: FC<IColumnHeader> = (props) => {
  const {
    column,
    title = '',
    enableDrag = true,
    hasSort = false,
    sorterField,
    quickCreateProfile = Promise.resolve,
  } = props;

  const { selectedTag } = useContext(tagsContext);
  const templateCtx = useContext(templatesContext);

  const containerClassName = (enableDrag) ? 'dragHandler' : '';

  const isShowAllTagsButtonShown = (column === 'tags' && selectedTag?.field === 'tags') ||
    (column === 'customStatus' && selectedTag?.field === 'custom-status');

  return (
    <ContainerNameCol className={containerClassName} isProxy={column === 'proxy'} newStyle={NEW_FEATURES.header}>
      <NameColText newStyle={!!NEW_FEATURES.header}>
        {title}
      </NameColText>
      {isShowAllTagsButtonShown ? <ShowAllTagsBtn field={selectedTag.field} /> : null}
      {hasSort && NEW_FEATURES.header ? <SorterCol sorterField={sorterField} /> : null}
      {column === 'name' ? (
        <ButtonQuickCreateProfile
          createProfile={(): Promise<void> => quickCreateProfile(templateCtx)}
        />
      ) : null}
    </ContainerNameCol>
  );
};

export default ColumnHeader;
