import { Checkbox } from 'antd';
import { CheckboxChangeEvent } from 'antd/lib/checkbox/Checkbox';
import React, { FC } from 'react';

import { useFilteredProxyList } from '../../../../state/proxy-list.atom';
import { useSelectedProxies } from '../../../../state/selected-proxies.atom';
import { IProxy } from '../../components/interfaces/IProxy';
import { selectProxyLI } from '../../proxy-helpers';
import { ProxyCheckboxWrapperEl } from './styles/proxy-item-checkbox';

interface IProps {
  proxy: IProxy;
}

const ProxyItemCheckbox: FC<IProps> = (props) => {
  const { proxy } = props;
  const { lastSelectedProxy, selectedProxies } = useSelectedProxies();
  const filteredProxies = useFilteredProxyList();

  const onCheck = (event: CheckboxChangeEvent): void => {
    event.stopPropagation();
    event.preventDefault();

    selectProxyLI({
      shiftKey: event.nativeEvent.shiftKey,
      selectedProxies,
      filteredProxies,
      proxy,
      lastSelectedProxy,
    });
  };

  return (
    <ProxyCheckboxWrapperEl>
      <Checkbox onChange={onCheck} checked={selectedProxies.includes(proxy._id)} />
    </ProxyCheckboxWrapperEl>
  );
};

export default ProxyItemCheckbox;
