import moment from 'moment';

import { createProxiesRequest } from './api';
import { IProxy } from './components/interfaces/IProxy';
import { getProxyStatusParams } from './proxy-helpers';
import { handleProxiesLimitError } from './utils/handle-proxies-limit-error';
import { setProxyStatuses } from '../../state/proxies.context/api';
import { updateProxyItem, updateProxyList } from '../../state/proxy-list.atom';

interface IParams {
  proxyList: IProxy[];
  proxies: IProxy[];
}

export const addMultipleProxies = async (params: IParams): Promise<IProxy[] | undefined> => {
  const { proxyList, proxies } = params;

  if (!proxies?.length) {
    return;
  }

  const addedProxies: IProxy[] = [];
  const newProxies: IProxy[] = [];
  const pasteProxyLimit = 50;

  const upsertedProxies: IProxy[] = await createProxiesRequest(proxies.slice(0, pasteProxyLimit))
    .catch(error => {
      handleProxiesLimitError(error);

      return [];
    });

  for (const proxy of upsertedProxies) {
    try {
      const existingProxy = proxyList.find((el) => (
        el._id === proxy._id
      )) as IProxy;

      if (existingProxy) {
        addedProxies.push(existingProxy);
        continue;
      }

      addedProxies.push(proxy);
      newProxies.push(proxy);
    } catch (err) {
      console.log(err);
    }
  }

  let updatedProxies: any[] = proxyList;

  const showInfo = (newProxies.length === 1);

  for (const newProxy of newProxies) {
    let proxyWasChanged = false;
    updatedProxies = updatedProxies.map(proxy => {
      if (proxy._id !== newProxy._id) {
        return proxy;
      }

      // Mark that proxy already in list and was changed
      proxyWasChanged = true;

      return {
        ...newProxy,
        checkInProgress: true,
        showInfoAfterCheck: showInfo,
      };
    });

    // If proxy doesn't exists - add proxy as first array element
    if (!proxyWasChanged) {
      updatedProxies.unshift({
        ...newProxy,
        checkInProgress: true,
        showInfoAfterCheck: showInfo,
      });
    }
  }

  updateProxyList(updatedProxies);

  for (const newProxy of addedProxies) {
    // TODO reduce requests count
    setTimeout(async (): Promise<void> => {
      const statusParams = await getProxyStatusParams({
        ...newProxy,
        showInfoAfterCheck: showInfo,
      });

      updateProxyItem({
        _id: newProxy._id,
        ...statusParams,
        checkDate: moment().toDate(),
        checkInProgress: false,
      });

      if (!statusParams) {
        return;
      }

      await setProxyStatuses([{
        _id: newProxy._id,
        status: statusParams.status,
        country: statusParams.country || newProxy.country,
        city: statusParams.city || newProxy.city,
        error: statusParams.error || null,
        checkDate: moment().unix(),
        lastIp: statusParams.origin,
      }]).catch(() => null);
    }, 0);
  }

  return addedProxies;
};
