import moment from 'moment';
import React, { FC, SyntheticEvent, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { IProxy } from './interfaces/IProxy';
import ProxyIconBtnWithTooltip from './proxy-icon-btn-with-tooltip';
import ProxyInfoError from './proxy-info-error';
import { updateEditModeProfileId, updateProxyElementLocation } from '../../../state/edit-mode-profile-id.atom';
import { useFreeProxies } from '../../../state/free-proxies.atom';
import { closeProxyManager, openProxyManagerEditView } from '../../../state/proxy-manager-modal-status.atom';
import IconCheckCircle from '../../../ui/icons/IconCheckCircle';
import IconCloseCurrentColor from '../../../ui/icons/IconCloseCurrentColor';
import IconProxyPencil from '../../../ui/icons/IconProxyPencil';
import IconWarningTr from '../../../ui/icons/IconWarningTr';
import countries from '../../common/countries';
import { sendChangeIpRequest } from '../../quickProfiles/api';
import { checkIsCurrent, getProxyStatus } from '../proxy-helpers';
import { ProxyStatusDot } from '../styles/check-proxy-btn';
import {
  ProxyInfoContainerEl,
  ProxyInfoButtonsEl,
  ProxyInfoRowEl,
  ProxyStatusIconEl,
  ProxyInfoUpdatedAgoEl,
  ChangeIpBtn,
  RemoveBtnContainerEl,
  ProxyStatusDotContainerEl,
} from '../styles/proxy-info';
import { ProxyButtonEl, RemoveButtonEl } from '../proxy-manager/proxy-list/styles/proxy-list-item';
import { TooltipEl } from '../proxy-selector/styles';
import { unselectProfileProxy } from '../../../state/proxy-operations.commands';

interface IProps {
  proxy: IProxy;
  currentProxy?: IProxy;
  profileId?: string;
  closeTooltip: () => void;
  isInForm?: boolean;
  setIsChecking: React.Dispatch<React.SetStateAction<boolean>>;
  updateProxyStatus: (event: React.MouseEvent, skipRowNode: boolean) => Promise<void>;
  localProxyElementLocation: string|null;
  isSharedProxy?: boolean;
  onProxyInfoEditClick?: () => void;
}

const ProxyInfo: FC<IProps> = (props) => {
  const {
    proxy,
    currentProxy,
    profileId,
    closeTooltip,
    isInForm,
    setIsChecking,
    updateProxyStatus,
    localProxyElementLocation,
    isSharedProxy,
    onProxyInfoEditClick,
  } = props;

  const [noProxyBtnVisible, setNoProxyBtnVisible] = useState<boolean>(false);

  const freeProxies = useFreeProxies();
  const isProxyModeEditable = ['http', 'socks4', 'socks5', 'geolocation'].includes(proxy.mode);
  const shouldShowEditButton = isProxyModeEditable && !(proxy.isInvisible || isSharedProxy);

  const handleMouseEnter = (): void => setNoProxyBtnVisible(true);
  const handleMouseLeave = (): void => setNoProxyBtnVisible(false);

  const { t: translation, i18n } = useTranslation();
  moment.locale(i18n.language);

  let checkedAgo = '';
  if (proxy.checkDate) {
    checkedAgo = moment(proxy.checkDate).fromNow();
  }

  let countryString = '';

  let proxyCountry = proxy.country;
  if (proxy.mode === 'tor') {
    proxyCountry = proxy.torProxyRegion?.toUpperCase();
  } else if (proxy.mode === 'gologin') {
    proxyCountry = proxy.autoProxyRegion?.toUpperCase();
  }

  if (proxyCountry) {
    const country = countries.find((el => el.code === proxyCountry));

    if (country) {
      countryString = [
        [proxy.city, country.name].filter(Boolean).join(', '),
        `(${proxyCountry})`,
      ].filter(Boolean).join(' ');
    }
  }

  const handleEditClick = async (event: SyntheticEvent): Promise<void> => {
    event.preventDefault();
    event.stopPropagation();

    if (profileId) {
      updateEditModeProfileId(profileId);
      updateProxyElementLocation(localProxyElementLocation);
    }

    onProxyInfoEditClick && onProxyInfoEditClick();
    openProxyManagerEditView(proxy._id);
    closeTooltip();
  };

  const changeIp: React.MouseEventHandler = async (event): Promise<void> => {
    event.preventDefault();
    event.stopPropagation();

    if (!proxy.changeIpUrl) {
      return;
    }

    setIsChecking(true);
    sendChangeIpRequest(proxy.changeIpUrl).catch(() => null).finally(() => {
      updateProxyStatus(event, true).catch(() => null).finally(() => setIsChecking(false));
    });
  };

  const handleNoProxyClick = (event: SyntheticEvent): void => {
    event.preventDefault();
    event.stopPropagation();

    if (profileId) {
      unselectProfileProxy(profileId);
      closeProxyManager();
    }
  };

  const isCurrent = checkIsCurrent(proxy, currentProxy);

  const isTorOrFree = ['gologin', 'tor'].includes(proxy.mode);
  let { host } = proxy;
  if (proxy.mode === 'gologin') {
    const freeProxy = freeProxies.find((el) => el.region === proxy.autoProxyRegion);
    if (freeProxy) {
      host = freeProxy.host;
    }

    countryString = [translation('proxies.freeProxy'), countryString].join(', ');
  } else if (proxy.mode === 'tor') {
    countryString = [translation('proxies.torProxy'), countryString].join(', ');
  }

  const renderCheckDate = (): JSX.Element | null => {
    if (isTorOrFree) {
      return (
        <ProxyInfoRowEl>
          <ProxyStatusDotContainerEl>
            <ProxyStatusDot status={'success'} />
          </ProxyStatusDotContainerEl>
          <ProxyInfoUpdatedAgoEl>
            {translation('proxies.connected')}
          </ProxyInfoUpdatedAgoEl>
        </ProxyInfoRowEl>
      );
    }

    const status = getProxyStatus(proxy);

    if (!proxy.checkDate) {
      return (
        <ProxyInfoRowEl>
          <ProxyStatusDotContainerEl>
            <ProxyStatusDot status={status} />
          </ProxyStatusDotContainerEl>
          <ProxyInfoUpdatedAgoEl>
            {translation('proxies.clickToCheckTheConnection')}
          </ProxyInfoUpdatedAgoEl>
        </ProxyInfoRowEl>
      );
    }

    return (
      <>
        {proxy.status === false ? (
          <>
            <ProxyInfoRowEl>
              <ProxyStatusDotContainerEl>
                <ProxyStatusDot status={status} />
              </ProxyStatusDotContainerEl>
              <ProxyInfoUpdatedAgoEl>
                {' '}
                {translation('proxies.updated')}
                {' '}
                {checkedAgo}
              </ProxyInfoUpdatedAgoEl>
            </ProxyInfoRowEl>
            <ProxyInfoRowEl>
              <ProxyStatusIconEl danger>
                <IconWarningTr />
              </ProxyStatusIconEl>
              <ProxyInfoUpdatedAgoEl danger>
                {translation(proxy.error || translation('notifications.error.proxyError'))}
              </ProxyInfoUpdatedAgoEl>
            </ProxyInfoRowEl>
          </>
        ) : (
          <ProxyInfoRowEl>
            <ProxyStatusDotContainerEl>
              <ProxyStatusDot status={status} />
            </ProxyStatusDotContainerEl>
            <ProxyInfoUpdatedAgoEl>
              {' '}
              {translation('proxies.updated')}
              {' '}
              {checkedAgo}
            </ProxyInfoUpdatedAgoEl>
          </ProxyInfoRowEl>
        )}
      </>
    );
  };

  const renderRemoveButton = (): JSX.Element|null => {
    if (!isCurrent) {
      return null;
    }

    return (
      <div
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
      >
        {(noProxyBtnVisible) ? (
          <RemoveBtnContainerEl>
            <ProxyIconBtnWithTooltip tooltipContent={(
              <TooltipEl>
                {translation('proxies.unlinkProxy')}
              </TooltipEl>
            )}>
              <RemoveButtonEl onClick={handleNoProxyClick}>
                <IconCloseCurrentColor />
              </RemoveButtonEl>
            </ProxyIconBtnWithTooltip>
          </RemoveBtnContainerEl>
        ) : (
          <ProxyButtonEl success>
            <IconCheckCircle />
          </ProxyButtonEl>
        )}
      </div>
    );
  };

  const renderEditButton = (): JSX.Element|null => {
    if (!shouldShowEditButton || isInForm) {
      return null;
    }

    return (
      <ProxyIconBtnWithTooltip tooltipContent={(
        <TooltipEl>
          {translation('proxies.editProxy')}
        </TooltipEl>
      )}>
        <ProxyButtonEl onClick={handleEditClick}>
          <IconProxyPencil />
        </ProxyButtonEl>
      </ProxyIconBtnWithTooltip>
    );
  };

  return (
    <ProxyInfoContainerEl onClick={(e): void => {
      e.stopPropagation();
    }}>
      <div>
        {renderCheckDate()}

        {(proxy.status === false) ? <ProxyInfoError proxy={proxy} /> : (
          <>
            {(countryString) ? (
              <ProxyInfoRowEl>
                {countryString}
              </ProxyInfoRowEl>
            ) : null}

            {(host && proxy.status) ? (
              <ProxyInfoRowEl>
                {proxy.origin || proxy.lastIp || host}
                {' '}
                {(proxy.changeIpUrl) ? (
                  <ChangeIpBtn onClick={changeIp}>
                    {translation('proxies.changeIp')}
                  </ChangeIpBtn>
                ) : null}
              </ProxyInfoRowEl>
            ) : null}
          </>
        )}
      </div>

      <ProxyInfoButtonsEl>
        {renderRemoveButton()}
        {renderEditButton()}
      </ProxyInfoButtonsEl>
    </ProxyInfoContainerEl>
  );
};

export default ProxyInfo;
