import React, { FC, SyntheticEvent } from 'react';
import { useTranslation } from 'react-i18next';

import PerformanceObserverService from '../../../../services/performance-observer/performance-observer.service';
import { IProxy } from '../../components/interfaces/IProxy';
import ProxyIconBtnWithTooltip from '../../components/proxy-icon-btn-with-tooltip';
import ProxyItem from './proxy-item';
import ProxyProfilesCountBlock from '../../components/proxy-profiles-count-block';
import { NEW_FEATURES } from '../../../../../../feature-toggle';
import { useFilteredProxyList } from '../../../../state/proxy-list.atom';
import { closeProxyManager, openProxyManagerEditView, useProxyManagerState } from '../../../../state/proxy-manager-modal-status.atom';
import { selectProfileProxy, unselectProfileProxy } from '../../../../state/proxy-operations.commands';
import {
  openProxyContextMenu,
  updateProxySelectMenuPosition,
  useIsSelectProxyModeOpened,
} from '../../../../state/proxy-select-menu.atom';
import { updateSelectedProxies, updateSelectedProxiesList, useSelectedProxies } from '../../../../state/selected-proxies.atom';
import IconCheckCircle from '../../../../ui/icons/IconCheckCircle';
import IconCloseCurrentColor from '../../../../ui/icons/IconCloseCurrentColor';
import IconProxyPencil from '../../../../ui/icons/IconProxyPencil';
import { selectProxyLI } from '../../proxy-helpers';
import {
  ProxyButtonEl,
  ProxyButtonsContainerEl,
  ProxyListItemEl,
  ProxyListItemInnerEl,
  RemoveButtonEl,
} from './styles/proxy-list-item';
import { TooltipEl } from '../../proxy-selector/styles';

interface IProps {
  proxy: IProxy;
  isCurrent: boolean;
  style: any;
  localProxyElementLocation: string | null;
}

const ProxyListItem: FC<IProps> = (props: IProps) => {
  const {
    proxy,
    isCurrent,
    style,
    localProxyElementLocation,
  } = props;

  const { lastSelectedProxy, selectedProxies } = useSelectedProxies();
  const isSelectProxyModeOpened = useIsSelectProxyModeOpened();
  const filteredProxies = useFilteredProxyList();
  const { currentProfileId } = useProxyManagerState();

  const { t: translation } = useTranslation();

  const handleClick = (event: React.MouseEvent<HTMLAnchorElement, MouseEvent>): void => {
    event.stopPropagation();
    event.preventDefault();

    if (isSelectProxyModeOpened) {
      updateSelectedProxiesList(proxy._id);

      selectProxyLI({
        shiftKey: event.shiftKey,
        selectedProxies,
        filteredProxies,
        proxy,
        lastSelectedProxy,
      });

      return;
    }

    if (currentProfileId) {
      const performanceObserverService = PerformanceObserverService.getInstance();
      performanceObserverService.handleUserAction({ userAction: 'select-proxy-manager-proxy' });

      selectProfileProxy(currentProfileId, proxy);
      closeProxyManager();
    }
  };

  const handleUpdateClick = async (event: SyntheticEvent): Promise<void> => {
    event.preventDefault();
    event.stopPropagation();

    openProxyManagerEditView(proxy._id);
  };

  const showBtns = ['http', 'socks4', 'socks5', 'geolocation'].includes(proxy.mode);

  const handleNoProxyClick = (event: SyntheticEvent): void => {
    event.preventDefault();
    event.stopPropagation();

    if (currentProfileId) {
      unselectProfileProxy(currentProfileId);
      closeProxyManager();
    }
  };

  let currentProxy: IProxy | undefined;
  if (isCurrent) {
    currentProxy = proxy;
  }

  return (
    <ProxyListItemEl
      onClick={handleClick}
      style={style}
      onContextMenu={(event): void => {
        event.preventDefault();
        updateSelectedProxies({ selectedProxy: proxy });
        updateProxySelectMenuPosition({
          x: event.pageX,
          y: event.pageY,
        });

        openProxyContextMenu();
      }}
    >
      <ProxyListItemInnerEl newStyle={!!NEW_FEATURES.header}>
        <ProxyItem
          proxy={proxy}
          currentProxy={currentProxy}
          profileId={currentProfileId}
          hideDot={false}
          showCheckbox={!!(proxy._id && isSelectProxyModeOpened)}
          localProxyElementLocation={localProxyElementLocation}
        />
        {(proxy?.profilesCount) ? (
          <ProxyProfilesCountBlock profilesCount={proxy.profilesCount} />
        ) : null}
      </ProxyListItemInnerEl>

      <ProxyButtonsContainerEl onClick={(event: SyntheticEvent): void => {
        event.preventDefault();
        event.stopPropagation();
      }}>
        {(showBtns) ? (
          <ProxyIconBtnWithTooltip tooltipContent={(
            <TooltipEl>
              {translation('proxies.editProxy')}
            </TooltipEl>
          )}>
            <ProxyButtonEl className='proxy-update-btn' onClick={handleUpdateClick}>
              <IconProxyPencil />
            </ProxyButtonEl>
          </ProxyIconBtnWithTooltip>
        ) : null}

        {(isCurrent) ? (
          <>
            <ProxyButtonEl className='proxy-selected'>
              <IconCheckCircle />
            </ProxyButtonEl>

            <ProxyIconBtnWithTooltip tooltipContent={(
              <TooltipEl>
                {translation('proxies.unlinkProxy')}
              </TooltipEl>
            )}>
              <RemoveButtonEl className='proxy-update-btn' onClick={handleNoProxyClick}>
                <IconCloseCurrentColor />
              </RemoveButtonEl>
            </ProxyIconBtnWithTooltip>
          </>
        ) : null}
      </ProxyButtonsContainerEl>
    </ProxyListItemEl>
  );
};

export default ProxyListItem;
