import styled from '@emotion/styled/macro';

export const AddFirstProfileTitle = styled.span`
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 28px;
  color: var(--B5B5BA-profile-table);
`;

export const FirstProfileButtonContainer = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 6px 12px;
  gap: 8px;
  width: fit-content;
  height: 32px;
  border: 1px solid var(--EBEBEB-profile-table);
  border-radius: 16px;
  margin: 32px auto auto;
  cursor: pointer;
`;
