import React from 'react';

import { IconSort, IconSortDown, IconSortUp } from '../gologin-header/icons';

export const SorterCol = ({ sorterField = '' }: { sorterField?: string }): JSX.Element|null => {
  const sortType = localStorage.getItem('sorterOrder');
  const sorterFieldLocal = localStorage.getItem('sorterField');
  const sortCurrentCol = sorterFieldLocal === sorterField;
  const iconProps = {
    padding: 4,
    iconColor: 'var(--98989F-profile-table-new-sorter)',
    iconHoveredColor: 'var(--2B2B31-profile-table-new-sorter)',
  };

  if (sortCurrentCol && sortType === 'ascend') {
    return (
      <IconSortUp {...iconProps} />
    );
  }

  if (sortCurrentCol && sortType === 'descend') {
    return (
      <IconSortDown {...iconProps} />
    );
  }

  return (
    <IconSort {...iconProps} />
  );
};
