import { useAtomValue } from 'jotai';
import React, { FC } from 'react';

import { NEW_FEATURES } from '../../../../../../feature-toggle';
import { IProxy } from '../../../../interfaces';
import { IBasicTableProfile } from '../../../../state/profiles-list.atom';
import { useProxyList } from '../../../../state/proxy-list.atom';
import { TextCell } from '../../table/styles';

interface IProps {
  profileAtom: IBasicTableProfile['atom'];
  paramName: keyof IProxy;
}

const ProxyParamCell: FC<IProps> = (props) => {
  const {
    profileAtom,
    paramName,
  } = props;

  let { proxy } = useAtomValue(profileAtom);
  const proxyList = useProxyList();

  let proxyRes = proxy;

  const proxyId = proxyRes.id;
  if (proxyId) {
    proxyRes = proxyList.find((el) => el._id === proxyId);

    if (!proxyRes) {
      proxyRes = proxy;
    }
  }

  proxy = proxyRes;

  if (!proxy || proxy.mode === 'none') {
    return (
      <div></div>
    );
  }

  if (NEW_FEATURES.header) {
    return (
      <TextCell>
        {proxy[paramName]}
      </TextCell>
    );
  }

  return (
    <div>
      {proxy[paramName]}
    </div>
  );
};

export default ProxyParamCell;
