import React, { FC } from 'react';

import ModernSwitch from '../../../../ui/modern-switch';
import { IChangeSetting } from '../../interfaces';
import { ContainerParams } from '../../styles';

interface IExtensionStorage extends IChangeSetting {
  extensions: boolean;
}
export const ExtensionStorage: FC<IExtensionStorage> = ({ changeSetting, extensions }) => (
  <ContainerParams>
    <ModernSwitch
      checked={extensions}
      onChange={(checked): void => changeSetting({ storage: { extensions: checked } })}
    />
  </ContainerParams>
);
