import React, { FC, SyntheticEvent } from 'react';
import { useTranslation } from 'react-i18next';

import { ProxyButtonsContainerEl } from './styles';
import { NEW_FEATURES } from '../../../../../../feature-toggle';
import useDelayedMount from '../../../../hooks/use-should-mount.hook';
import { openProxyManagerAddView } from '../../../../state/proxy-manager-modal-status.atom';
import { pasteProxies } from '../../../../state/proxy-operations.commands';
import IconPasteProxy from '../../../../ui/icons/IconPasteProxy';
import IconPlus from '../../../../ui/icons/IconPlus';
import ProxyIconBtnWithTooltip from '../../components/proxy-icon-btn-with-tooltip';
import { IconBtn } from '../../styles/icon-btns';
import { TooltipEl } from '../styles';

interface IProps {
  isVisible?: boolean;
  editProxyMode: boolean;
  profileId: string;
  isInDrawer?: boolean;
  setShowTooltipCheckProxy: (state: boolean) => void;
}

const ProxySelectorButtons: FC<IProps> = (props) => {
  const { isVisible, editProxyMode, profileId, isInDrawer, setShowTooltipCheckProxy } = props;
  const isInNewDrawer = isInDrawer && NEW_FEATURES.drawer;

  const { t: translation } = useTranslation();

  const { shouldMount } = useDelayedMount(!!isVisible);
  if (!shouldMount) {
    return null;
  }

  const handleAddClick = (event: SyntheticEvent): void => {
    event.preventDefault();
    openProxyManagerAddView();
  };

  const handlePasteClick = async (event: SyntheticEvent): Promise<void> => {
    event.stopPropagation();
    event.preventDefault();
    setShowTooltipCheckProxy(true);

    await pasteProxies(profileId);
  };

  return (
    <ProxyButtonsContainerEl className='proxy-btns' editProxyMode={editProxyMode} newStyle={!!NEW_FEATURES.header} isVisible={isVisible}>
      <ProxyIconBtnWithTooltip tooltipContent={(
        <TooltipEl>
          {translation('proxies.addProxy')}
        </TooltipEl>
      )}>
        <IconBtn isInNewDrawer={isInNewDrawer} onClick={handleAddClick}>
          <IconPlus />
        </IconBtn>
      </ProxyIconBtnWithTooltip>

      <ProxyIconBtnWithTooltip tooltipContent={(
        <TooltipEl>
          {translation('proxies.pasteProxies')}
        </TooltipEl>
      )}>
        <IconBtn isInNewDrawer={isInNewDrawer} onClick={handlePasteClick}>
          <IconPasteProxy />
        </IconBtn>
      </ProxyIconBtnWithTooltip>
    </ProxyButtonsContainerEl>
  );
};

export default ProxySelectorButtons;
