import React, { FC, memo } from 'react';
import { areEqual } from 'react-window';

import { checkIsCurrent } from '../../proxy-helpers';
import { IProfileProxy } from '../../components/interfaces/IProfileProxy';
import { IProxy } from '../../components/interfaces/IProxy';
import ProxyListItem from './proxy-list-item';

interface IProps {
  data: {
    filteredProxies: (IProxy|null)[];
    currentProxy?: IProfileProxy;
    onProxySelect: (proxy: IProxy, profileId?: string) => Promise<void>;
    onSetNoProxy: (profileId: string) => Promise<void>;
    localProxyElementLocation: string | null;
  };
  index: number;
  style: any;
}

const ProxyRow: FC<IProps> = (props) => {
  const { data, index, style } = props;

  const {
    filteredProxies,
    currentProxy,
    localProxyElementLocation,
  } = data;

  const proxy: IProxy = filteredProxies[index] as IProxy;

  if (!proxy) {
    return <div></div>;
  }

  return (
    <ProxyListItem
      key={[
        proxy.mode,
        proxy.host,
        proxy.port,
        proxy.username,
        proxy.password,
        proxy.autoProxyRegion,
        proxy.torProxyRegion,
        proxy._id,
      ].join('_')}
      proxy={proxy}
      isCurrent={checkIsCurrent(proxy, currentProxy)}
      style={style}
      localProxyElementLocation={localProxyElementLocation}
    />
  );
};

export default memo(ProxyRow, areEqual);
