import React, { FC, useEffect, useRef } from 'react';
import { FixedSizeList } from 'react-window';

import EmptyList from './empty-list';
import ProxyRow from './proxy-row';
import { ProxyListContentEl } from './styles/proxy-list';
import { useFilteredProxyList } from '../../../../state/proxy-list.atom';
import { getProxyManagerState, setProxyManagerState } from '../../../../state/proxy-manager-modal-status.atom';
import {
  switchIsSelectProxyModeOpened,
  useConfirmBlockVisible,
  useIsSelectProxyModeOpened,
  useIsProxyContextMenuVisible, hideProxyContextMenu,
} from '../../../../state/proxy-select-menu.atom';
import { useSelectedProxies } from '../../../../state/selected-proxies.atom';
import ContextMenu from '../../components/context-menu';
import { IProfileProxy } from '../../components/interfaces/IProfileProxy';
import { IProxy } from '../../components/interfaces/IProxy';
import ProxyListMenu from '../../components/proxy-list-menu';
import RemovingConfirm from '../../components/removing-confirm';

interface IProps {
  currentProxy?: IProfileProxy;
  localProxyElementLocation: string | null;
}

const ProxyList: FC<IProps> = (props: IProps) => {
  const {
    currentProxy,
    localProxyElementLocation,
  } = props;

  const width = 330;

  const isSelectProxyModeOpened = useIsSelectProxyModeOpened();
  const confirmBlockVisible = useConfirmBlockVisible();
  const isProxyContextMenuVisible = useIsProxyContextMenuVisible();

  const ref = useRef<HTMLDivElement>(null);

  const filteredProxies = useFilteredProxyList();
  const { selectedProxies } = useSelectedProxies();

  useEffect(() => {
    if (currentProxy?.mode !== 'none') {
      const proxyManagerState = getProxyManagerState();
      setProxyManagerState({ ...proxyManagerState, currentProxy });
    }

    return () => {
      const proxyManagerState = getProxyManagerState();
      setProxyManagerState({ ...proxyManagerState, currentProxy: null });
    };
  }, []);

  useEffect(() => {
    if (!selectedProxies.length) {
      switchIsSelectProxyModeOpened(false);
    }
  }, [selectedProxies]);

  useEffect(() => {
    const handleScroll = (): void => {
      hideProxyContextMenu();
    };

    if (ref?.current) {
      if (isProxyContextMenuVisible) {
        ref.current.addEventListener('scroll', handleScroll);
      } else {
        ref.current.removeEventListener('scroll', handleScroll);
      }
    }

    return (): void => {
      if (ref?.current) {
        ref.current.removeEventListener('scroll', handleScroll);
      }
    };
  }, [isProxyContextMenuVisible]);

  let proxiesRes: Array<IProxy|null> = filteredProxies.filter(proxy => !proxy.isInvisible);
  proxiesRes = isSelectProxyModeOpened ? [...proxiesRes, null] : proxiesRes;

  const renderConfirm = (): JSX.Element | null => {
    if (!confirmBlockVisible) {
      return null;
    }

    return <RemovingConfirm />;
  };

  if (!proxiesRes.length) {
    return <EmptyList />;
  }

  return (
    <>
      <ProxyListContentEl>
        <div style={{ width }}>
          <FixedSizeList
            height={278}
            width={width}
            itemCount={proxiesRes.length}
            itemData={{
              filteredProxies: proxiesRes,
              currentProxy,
              localProxyElementLocation,
            }}
            itemSize={41}
            outerRef={ref}
          >
            {ProxyRow}
          </FixedSizeList>
        </div>
        <ProxyListMenu />
      </ProxyListContentEl>
      <ContextMenu />
      {renderConfirm()}
    </>
  );
};

export default ProxyList;
