import { message } from 'antd';
import moment from 'moment';
import React, { FC, useState, ChangeEvent, useEffect, SyntheticEvent, ClipboardEvent, MouseEvent } from 'react';
import { useTranslation } from 'react-i18next';

import ProxyUpdateFormHeader from './proxy-update-form-header';
import {
  DeleteProxyBtn,
  FormButtonsContainer,
  HostPortSplitter,
  ProxyFormContent,
  ProxyFormInputEl,
  ProxyFormLeft,
  ProxyFormRight,
  ProxyFormRowEl,
} from './styles';
import { NEW_FEATURES } from '../../../../../../feature-toggle';
import { useProxyList } from '../../../../state/proxy-list.atom';
import { openProxyManagerListView, useProxyManagerState } from '../../../../state/proxy-manager-modal-status.atom';
import { switchConfirmBlockVisible } from '../../../../state/proxy-select-menu.atom';
import { updateSelectedProxies } from '../../../../state/selected-proxies.atom';
import { isString } from '../../../../utils/is-string';
import { parseMultipleProxies, proxyFromClipboard } from '../../../../utils/proxy-string';
import { isProxyDataValid } from '../../../../utils/proxy.validation-message';
import { sendActionAnalytics } from '../../../common/api';
import { saveProxy } from '../../api';
import CheckProxyBtn from '../../components/check-proxy-btn';
import { IProxy } from '../../components/interfaces/IProxy';
import ProxyFlag from '../../components/proxy-flag';
import RadioButtons from '../../components/radio-buttons';
import { FORM_VALUES_DEFAULT } from '../../constants';
import { copyProxies, IStatusParams, proxyModes, getProxyStatusParams } from '../../proxy-helpers';
import { ProxyFlagBlockEl } from '../proxy-list/styles/proxy-list-item';

interface IProps {
  currentProxy?: IProxy;
  localProxyElementLocation: string | null;
}

const ProxyManagerEditView: FC<IProps> = (props) => {
  const { currentProxy, localProxyElementLocation } = props;
  const [formValues, setFormValues] = useState<IProxy>(FORM_VALUES_DEFAULT);
  const [isChecking, setIsChecking] = useState(false);

  const { modalView, modalEditingProxyId, currentProfileId } = useProxyManagerState();
  const proxyList = useProxyList();

  const { t: translation } = useTranslation();

  useEffect(() => {
    const fillForm = async (): Promise<void> => {
      let selectedProxy;
      if (modalView === 'proxy-edit' && modalEditingProxyId) {
        selectedProxy = proxyList.find((el) => el._id === modalEditingProxyId);
      } else if (modalView === 'proxy-paste') {
        selectedProxy = await proxyFromClipboard();
      }

      if (selectedProxy) {
        setFormValues({
          _id: selectedProxy._id || '',
          customName: selectedProxy.customName || '',
          mode: selectedProxy.mode || 'http',
          host: selectedProxy.host || '',
          port: (selectedProxy.port) ? selectedProxy.port : '',
          username: selectedProxy.username || '',
          password: selectedProxy.password || '',
          changeIpUrl: selectedProxy.changeIpUrl || '',
          status: selectedProxy.status || false,
          country: selectedProxy.country,
          city: selectedProxy.city,
          error: selectedProxy.error,
          checkDate: selectedProxy.checkDate,
        });
      } else {
        setFormValues(FORM_VALUES_DEFAULT);
      }
    };

    fillForm();
  }, [modalEditingProxyId, modalView]);

  const handleChangeMode = (event: ChangeEvent<HTMLInputElement>): void => {
    setFormValues({
      ...formValues,
      mode: event.target.value,
    });
  };

  const handleChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setFormValues({
      ...formValues,
      [event.target.name]: event.target.value,
    });
  };

  const handleSubmit = async (event: SyntheticEvent): Promise<void> => {
    event.preventDefault();

    const {
      customName,
      mode,
      host,
      port,
      username,
      password,
      changeIpUrl,
    } = formValues;

    if (!host && !port && !username && !password) {
      openProxyManagerListView();

      return;
    }

    const isProxyValid = isProxyDataValid({ host, port: port.toString(), translation });
    if (!isProxyValid) {
      return;
    }

    const proxyItem = {
      mode,
      customName: (customName || '').trim(),
      host: host.replaceAll(' ', ''),
      port: parseInt(`${port}`.replaceAll(' ', ''), 10),
      username: (username || '').replaceAll(' ', ''),
      password: (password || '').replaceAll(' ', ''),
      changeIpUrl: (changeIpUrl || '').replaceAll(' ', ''),
    };

    const proxyId = modalEditingProxyId;

    if (modalEditingProxyId && modalView === 'proxy-edit') {
      const selectedProxy = proxyList.find((el) => el._id === modalEditingProxyId);

      if (
        selectedProxy &&
        selectedProxy.mode === proxyItem.mode &&
        selectedProxy.customName === proxyItem.customName &&
        selectedProxy.host === proxyItem.host &&
        selectedProxy.port === proxyItem.port &&
        selectedProxy.username === proxyItem.username &&
        selectedProxy.password === proxyItem.password &&
        selectedProxy.changeIpUrl === proxyItem.changeIpUrl
      ) {
        openProxyManagerListView();

        return;
      }
    }

    setIsChecking(true);

    await saveProxy(
      { proxyId, proxyItem, checkProxy: true },
      currentProfileId || '',
    )
      .catch(() => null)
      .finally(() => setIsChecking(false));

    openProxyManagerListView();
  };

  const pasteProxyText = async (clipboardText: string): Promise<IStatusParams | undefined>  => {
    const proxies = await parseMultipleProxies(clipboardText);
    if (!proxies?.length) {
      return;
    }

    const [parsedProxy] = proxies;

    if (!parsedProxy) {
      return;
    }

    const newProxy = {
      _id: '',
      mode: (isString(parsedProxy.mode)) ? parsedProxy.mode : formValues.mode,
      customName: (isString(parsedProxy.customName)) ? parsedProxy.customName : formValues.customName,
      host: (isString(parsedProxy.host)) ? parsedProxy.host : formValues.host,
      port: parsedProxy.port || formValues.port,
      username: (isString(parsedProxy.username)) ? parsedProxy.username : formValues.username,
      password: (isString(parsedProxy.password)) ? parsedProxy.password : formValues.password,
      changeIpUrl: (isString(parsedProxy.changeIpUrl)) ? parsedProxy.changeIpUrl : formValues.changeIpUrl,
      checkInProgress: true,
      showInfoAfterCheck: true,
    };

    setFormValues(newProxy);

    const statusParams = await getProxyStatusParams({
      ...newProxy,
      showInfoAfterCheck: true,
    });

    setFormValues({
      ...newProxy,
      ...statusParams,
      checkDate: moment().toDate(),
      checkInProgress: false,
    });

    sendActionAnalytics('pasted proxy from buffer');

    return statusParams;
  };

  const handlePaste = async (event: ClipboardEvent<HTMLInputElement>): Promise<void | true> => {
    const clipboardText = event.clipboardData.getData('Text');
    const pasteProxyRes = await pasteProxyText(clipboardText);

    if (pasteProxyRes) {
      event.preventDefault();
    }
  };

  const handlePasteChangeIpUrl = async (event: ClipboardEvent<HTMLInputElement>): Promise<void> => {
    const clipboardText = event.clipboardData.getData('Text');
    const isPossibleUrl = clipboardText.startsWith('http://') || clipboardText.startsWith('https://');
    if (isPossibleUrl) {
      // without event.preventDefault - pastes into field, without parsing
      return;
    }

    const pasteProxyRes = await pasteProxyText(clipboardText);
    if (pasteProxyRes) {
      event.preventDefault();
    }
  };

  const handlePasteProxy = async (): Promise<void | true> => {
    const clipboardText = await navigator.clipboard.readText();
    await pasteProxyText(clipboardText);
  };

  const deleteProxy = (event: MouseEvent): void => {
    event.stopPropagation();
    event.preventDefault();

    const proxyToDelete = proxyList.find((el) => el._id === modalEditingProxyId);
    updateSelectedProxies({ selectedProxy: proxyToDelete || null });
    openProxyManagerListView();
    switchConfirmBlockVisible(true);
  };

  const clearForm = (): void => {
    setFormValues(FORM_VALUES_DEFAULT);
  };

  const copyProxy = (): void => {
    copyProxies([formValues]);
    message.success(translation('base.copiedText'));
  };

  const renderFormFields = (): JSX.Element => (
    <>
      <ProxyFormRowEl style={{ paddingTop: 1 }}>
        <ProxyFormLeft>
          {translation('proxies.formFields.type')}
        </ProxyFormLeft>
        <ProxyFormRight>
          <RadioButtons
            options={proxyModes}
            value={formValues.mode}
            onChange={handleChangeMode}
          />
        </ProxyFormRight>
      </ProxyFormRowEl>
      <ProxyFormRowEl>
        <ProxyFormLeft>
          {translation('proxies.formFields.customName')}
        </ProxyFormLeft>
        <ProxyFormRight>
          <ProxyFormInputEl
            value={formValues.customName}
            name="customName"
            onChange={handleChange}
            onPaste={handlePaste}
            placeholder={translation('proxies.formFields.customNamePlaceholder') || ''}
          />
        </ProxyFormRight>
      </ProxyFormRowEl>
      <ProxyFormRowEl>
        <ProxyFormLeft style={{ width: 40 }}>
          {translation('proxies.formFields.host')}
        </ProxyFormLeft>
        <ProxyFormRight style={{ marginLeft: NEW_FEATURES.header ? 82 : 57 }}>
          <ProxyFlagBlockEl
            newStyle={!!NEW_FEATURES.header}
            style={{
              opacity: (formValues.host && formValues.port) ? 1 : 0,
            }}
          >
            <CheckProxyBtn
              proxy={{ ...formValues, checkInProgress: isChecking }}
              currentProxy={currentProxy}
              updateInForm={(proxyData): void => {
                setFormValues({
                  ...formValues,
                  ...proxyData,
                });
              }}
              profileId={currentProfileId}
              showTooltipCheckProxy={true}
              localProxyElementLocation={localProxyElementLocation}
            />
            {(formValues.country) ? <ProxyFlag region={formValues.country} /> : null}
          </ProxyFlagBlockEl>
          <ProxyFormInputEl
            value={formValues.host}
            name="host"
            onChange={handleChange}
            onPaste={handlePaste}
            placeholder={translation('proxies.formFields.hostPlaceholder') || ''}
            inputWidth={111}
          />
          <HostPortSplitter>
            :
          </HostPortSplitter>
          <ProxyFormInputEl
            value={formValues.port}
            name="port"
            onChange={handleChange}
            onPaste={handlePaste}
            placeholder={translation('proxies.formFields.portPlaceholder') || ''}
            inputWidth={58}
          />
        </ProxyFormRight>
      </ProxyFormRowEl>
      <ProxyFormRowEl>
        <ProxyFormLeft>
          {translation('proxies.formFields.username')}
        </ProxyFormLeft>
        <ProxyFormRight>
          <ProxyFormInputEl
            value={formValues.username}
            autoComplete="new-password"
            name="username"
            onChange={handleChange}
            onPaste={handlePaste}
            placeholder={translation('proxies.formFields.usernamePlaceholder') || ''}
          />
        </ProxyFormRight>
      </ProxyFormRowEl>
      <ProxyFormRowEl>
        <ProxyFormLeft>
          {translation('proxies.formFields.password')}
        </ProxyFormLeft>
        <ProxyFormRight>
          <ProxyFormInputEl
            value={formValues.password}
            autoComplete="new-password"
            name="password"
            type="text"
            onChange={handleChange}
            onPaste={handlePaste}
            placeholder={translation('proxies.formFields.passwordPlaceholder') || ''}
            data-lpignore="true"
          />
        </ProxyFormRight>
      </ProxyFormRowEl>
      <ProxyFormRowEl>
        <ProxyFormLeft>
          {translation('proxies.formFields.changeIpUrl')}
        </ProxyFormLeft>
        <ProxyFormRight>
          <ProxyFormInputEl
            value={formValues.changeIpUrl}
            name="changeIpUrl"
            onChange={handleChange}
            onPaste={handlePasteChangeIpUrl}
            placeholder={translation('proxies.formFields.changeIpUrlPlaceholder') || ''}
          />
        </ProxyFormRight>
      </ProxyFormRowEl>
    </>
  );

  return (
    <form onSubmit={handleSubmit} autoComplete='off'>
      <ProxyFormContent>
        {renderFormFields()}
        {(modalEditingProxyId) ? (
          <FormButtonsContainer>
            <DeleteProxyBtn onClick={deleteProxy} type='button'>
              {translation('proxies.deleteProxy')}
            </DeleteProxyBtn>
          </FormButtonsContainer>
        ) : null}
      </ProxyFormContent>
      <ProxyUpdateFormHeader
        clearForm={clearForm}
        copyProxy={copyProxy}
        handlePasteProxy={handlePasteProxy}
      />
    </form>
  );
};

export default ProxyManagerEditView;
