import React, { useEffect, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { IconWrapper, ProfileSettingsBtnWrapper, Text } from './styles';
import ProfileSettingsBtnTooltip from './tooltip';
import { NEW_FEATURES } from '../../../../../../feature-toggle';
import IconProfileSettingsBtnArrow from '../../../../ui/icons/IconProfileSettingsBtnArrow';
import TooltipCustom from '../../../../ui/tooltip-custom';

interface IProfileSettingsBtn {
  openProfileSettings: (event: React.MouseEvent) => void;
}

const ProfileSettingsBtn: React.FC<IProfileSettingsBtn> = (props) => {
  const { openProfileSettings } = props;

  const [anchorEl, setAnchorEl] = useState<HTMLElement | null>(null);
  const [isTooltipVisible, setIsTooltipVisible] = useState<boolean>(false);
  const [anchorElTooltip, setAnchorElTooltip] = useState<HTMLElement|null>(null);

  const [shouldMount, setShouldMount] = useState<boolean>(false);

  const wrapperRef = useRef<HTMLDivElement|null>(null);

  const { t: translation } = useTranslation();

  useEffect(() => {
    if (shouldMount) {
      return;
    }

    window.requestIdleCallback(
      () => setShouldMount(true),
      { timeout: 400 },
    );
  }, [shouldMount]);

  useEffect(() => {
    setAnchorEl(wrapperRef.current);
  }, []);

  if (!shouldMount) {
    return null;
  }

  const renderTooltip = (): JSX.Element => {
    if (NEW_FEATURES.header) {
      return (
        <TooltipCustom
          anchorEl={anchorElTooltip}
          value={translation('quickSettings.openQuickSettingBtnText') || 'Open quick profile settings'}
        />
      );
    }

    return (
      <ProfileSettingsBtnTooltip
        isVisible={isTooltipVisible}
        anchorEl={anchorEl}
      >
        {translation('quickSettings.openQuickSettingBtnText') || 'Open quick profile settings'}
      </ProfileSettingsBtnTooltip>
    );
  };

  return (
    <>
      <ProfileSettingsBtnWrapper
        ref={wrapperRef}
        onClick={openProfileSettings}
        onMouseEnter={(event): void => {
          NEW_FEATURES.header ? setAnchorElTooltip(event.currentTarget) : setIsTooltipVisible(true);
        }}
        onMouseLeave={(): void => {
          NEW_FEATURES.header ? setAnchorElTooltip(null) : setIsTooltipVisible(false);
        }}
        newStyle={!!NEW_FEATURES.header}
        newDrawer={!!NEW_FEATURES.drawer}
      >
        <Text>
          {translation(NEW_FEATURES.drawer ? 'profilesTable.openQuickSettings.settings' : 'quickSettings.details')}
        </Text>
        <IconWrapper>
          <IconProfileSettingsBtnArrow />
        </IconWrapper>
      </ProfileSettingsBtnWrapper>
      {renderTooltip()}
    </>
  );
};

export default React.memo(ProfileSettingsBtn);
