import Popper from '@material-ui/core/Popper';
import moment from 'moment';
import React, { FC, useState, useEffect, useRef } from 'react';

import { IProxy } from './interfaces/IProxy';
import { IUpdateSharedProxyInfo } from './interfaces/ISharedProxyInfo';
import ProxyInfo from './proxy-info';
import { NEW_FEATURES } from '../../../../../feature-toggle';
import { setProxyStatuses, setSharedProxyStatuses } from '../../../state/proxies.context/api';
import { IUpdateProxyItem } from '../../../state/proxies.context/interfaces/update-proxy-item.interface';
import { updateProxyItem } from '../../../state/proxy-list.atom';
import { updateSharedProxyInfo } from '../../../state/shared-proxy-info.atom';
import { useCurrentTheme } from '../../../state/theme.atom';
import getSelectedThemeColor from '../../../utils/get-selected-theme-color';
import { getProxyStatus, getProxyStatusParams } from '../proxy-helpers';
import { CheckProxyBtnContainerEl, FormIcon, ProxyStatusDot, TooltipEl, TooltipWrapperEl } from '../styles/check-proxy-btn';

interface IProps {
  proxy: IProxy;
  currentProxy?: IProxy;
  profileId?: string;
  hideDot?: boolean;
  updateInForm?: (proxyData: Partial<IProxy>) => void;
  saveProfileProxyStatus?: () => Promise<IProxy | undefined>;
  isSharedProxy?: boolean;
  isInTable?: boolean;
  showTooltipCheckProxy?: boolean;
  setShowTooltipCheckProxy?: (state: boolean) => void;
  localProxyElementLocation: string | null;
  onProxyInfoEditClick?: () => void;
}

const CheckProxyBtn: FC<IProps> = (props) => {
  const {
    proxy,
    currentProxy,
    profileId,
    hideDot,
    updateInForm,
    saveProfileProxyStatus,
    isSharedProxy,
    isInTable,
    showTooltipCheckProxy,
    setShowTooltipCheckProxy,
    localProxyElementLocation,
    onProxyInfoEditClick,
  } = props;

  const [tooltipVisible, setTooltipVisible] = useState<boolean>(false);
  const [btnVisible, setBtnVisible] = useState<boolean>(false);
  const [isChecking, setIsChecking] = useState<boolean>(false);
  const [showInfoAfterCheck, setShowInfoAfterCheck] = useState<boolean>(false);
  const tooltipRef = useRef<HTMLDivElement>(null);
  const theme = useCurrentTheme();

  let showSpinner = proxy.checkInProgress || isChecking;
  const isDarkTheme = getSelectedThemeColor(theme) === 'dark';

  const closeTooltip = (): void => {
    setTooltipVisible(false);
    if (setShowTooltipCheckProxy) {
      setShowTooltipCheckProxy(false);
    }
  };

  const openTooltip = (): void => {
    setTooltipVisible(true);
    if (setShowTooltipCheckProxy) {
      setShowTooltipCheckProxy(true);
    }
  };

  useEffect(() => {
    showSpinner = proxy.checkInProgress || isChecking;
  }, [proxy.checkInProgress]);

  useEffect(() => {
    let timeout: any;
    if (!tooltipVisible) {
      if (btnVisible && !showSpinner) {
        setIsChecking(false);
        setBtnVisible(false);
      }

      return;
    }

    if (proxy.showInfoAfterCheck || showInfoAfterCheck) {
      timeout = setTimeout(() => closeTooltip(), 2 * 1000);

      updateProxyItem({
        _id: proxy._id,
        showInfoAfterCheck: false,
        checkInProgress: false,
      });

      setShowInfoAfterCheck(false);
    }

    const root = document.getElementById('root');
    root?.addEventListener('scroll', closeTooltip, true);

    return () => {
      if (timeout) {
        clearTimeout(timeout);
      }

      root?.removeEventListener('keydown', closeTooltip, true);
    };
  }, [tooltipVisible]);

  useEffect(() => {
    if (
      !proxy.checkInProgress &&
      proxy.showInfoAfterCheck &&
      (hideDot || updateInForm)
    ) {
      setTooltipVisible(true);
    }
  }, [proxy]);

  const updateProxyStatus = async (event: React.MouseEvent, skipRowNode = false): Promise<void> => {
    event.stopPropagation();
    event.preventDefault();
    let rowNode: HTMLElement|null = null;
    if (setShowTooltipCheckProxy) {
      setShowTooltipCheckProxy(true);
    }

    if (isInTable && !skipRowNode) {
      rowNode = event.currentTarget.closest('tr');
      rowNode?.classList.add('row-visible-dot');
      rowNode?.classList.add('check-in-progress');
    }

    const { mode, host, port, username, password } = proxy;
    let proxyRes = proxy;

    setIsChecking(true);

    if (!isSharedProxy && saveProfileProxyStatus) {
      const savingResult = await saveProfileProxyStatus();
      if (savingResult) {
        proxyRes = savingResult;
      }
    }

    const { id: proxyId } = proxyRes as any;
    if (proxyId) {
      proxyRes._id = proxyId;
    }

    const statusParams = await getProxyStatusParams({
      ...proxyRes,
      mode,
      host,
      port,
      username,
      password,
    });

    const updateParams: IUpdateProxyItem = {
      ...proxy,
      ...statusParams,
      checkDate: moment().toDate(),
      checkInProgress: false,
      shouldAddInvisible: true,
    };

    if (isSharedProxy && profileId) {
      const updateParamsSharedProxy: IUpdateSharedProxyInfo = {
        ...updateParams,
        profileId,
      };

      updateSharedProxyInfo(updateParamsSharedProxy);
    } else if (updateInForm) {
      updateInForm({ ...updateParams, _id: proxyRes._id });
    } else {
      updateProxyItem({ ...updateParams, _id: proxyRes._id });
    }

    if (isInTable && !skipRowNode) {
      setShowInfoAfterCheck(true);
      openTooltip();

      if (rowNode) {
        rowNode.classList.remove('check-in-progress');
      }
    }

    if (!statusParams || updateInForm) {
      setIsChecking(false);

      return;
    }

    const checkParams: any = {
      status: statusParams.status,
      country: statusParams.country || proxyRes.country,
      city: statusParams.city || proxyRes.city,
      error: statusParams.error || null,
      checkDate: moment().unix(),
      lastIp: statusParams.origin,
      timezone: statusParams.timezone,
      languages: statusParams.languages,
    };

    if (isSharedProxy && profileId) {
      checkParams.profileId = profileId;
      await setSharedProxyStatuses([checkParams]);
    } else if (proxyRes._id) {
      checkParams._id = proxyRes._id;
      await setProxyStatuses([checkParams]).catch(() => null);
    }

    setIsChecking(false);
  };

  const isTorOrFree = ['gologin', 'tor'].includes(proxy.mode);

  const handleMouseEnter = (): void => {
    openTooltip();
    if (proxy.mode === 'none' || isTorOrFree) {
      return;
    }

    setBtnVisible(true);
  };

  const handleMouseLeave = (): void => {
    setBtnVisible(false);
    closeTooltip();
  };

  const hideBtn = (proxy.mode === 'none');

  if (hideBtn) {
    return <CheckProxyBtnContainerEl btnVisible={false} tooltipVisible={tooltipVisible} newStyle={!!NEW_FEATURES.header} />;
  }

  const hideDotResult = (showSpinner || tooltipVisible) ? false : hideDot;

  const renderDot = (): JSX.Element => {
    if (btnVisible || showSpinner) {
      return (
        <FormIcon
          type='sync'
          spin={showSpinner}
          className='proxy-status-icon'
          onClick={updateProxyStatus}
        />
      );
    }

    const status = getProxyStatus(proxy);

    return (
      <ProxyStatusDot
        status={status}
        className='proxy-status-dot'
      />
    );
  };

  return (
    <CheckProxyBtnContainerEl
      btnVisible={btnVisible || isChecking}
      hideDot={hideDotResult}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      onClick={(event): void => event.stopPropagation()}
      ref={tooltipRef}
      newStyle={!!NEW_FEATURES.header}
      tooltipVisible={tooltipVisible}
    >
      {renderDot()}

      <Popper
        open={!!(!isChecking && tooltipVisible && tooltipRef.current && showTooltipCheckProxy)}
        anchorEl={tooltipRef.current}
        style={{ zIndex: 1070 }}
        placement='top'
      >
        <TooltipWrapperEl>
          <TooltipEl isDarkTheme={isDarkTheme}>
            <ProxyInfo
              proxy={proxy}
              currentProxy={currentProxy}
              profileId={profileId}
              closeTooltip={closeTooltip}
              isInForm={!!updateInForm}
              setIsChecking={setIsChecking}
              updateProxyStatus={updateProxyStatus}
              localProxyElementLocation={localProxyElementLocation}
              isSharedProxy={isSharedProxy}
              onProxyInfoEditClick={onProxyInfoEditClick}
            />
          </TooltipEl>
        </TooltipWrapperEl>
      </Popper>
    </CheckProxyBtnContainerEl>
  );
};

export default CheckProxyBtn;
