import React, { FC } from 'react';

import ModernSwitch from '../../../../ui/modern-switch';
import { IChangeSetting } from '../../interfaces';
import { ContainerParams } from '../../styles';

interface IPasswordSaving extends IChangeSetting {
  passwords: boolean;
}

export const PasswordSaving: FC<IPasswordSaving> = ({ changeSetting, passwords }) => (
  <ContainerParams>
    <ModernSwitch
      checked={passwords}
      onChange={(checked): void => changeSetting({ storage: { passwords: checked } })}
    />
  </ContainerParams>
);
