import * as Sentry from '@sentry/react';

import { findProxyInList } from './find-proxy-in-list';
import { ReactError } from '../../../utils/sentry-parameters/custom-errors';
import { IProfilesResponse } from '../../quickProfiles/api';
import { IProxy } from '../components/interfaces/IProxy';
import { PROFILES_OFFSET_TAG, PROXIES_LOADED_COUNT_TAG, PROXIES_NOT_FOUND_COUNT, PROXY_LOADING_DELAYED_WARNING, PROXY_MODE_TAG } from '../constants';

const captureProxyLoadingDelayed = ({
  mainProxiesLength = 0,
  proxiesNotFoundCount = 0,
  offset = 0,
}: {
  mainProxiesLength: number;
  proxiesNotFoundCount: number;
  offset: number;
}): void => {
  Sentry.captureException(new ReactError(PROXY_LOADING_DELAYED_WARNING), (scope) => {
    scope.setLevel('warning');
    scope.setTransactionName(PROXY_LOADING_DELAYED_WARNING);
    scope.setFingerprint([PROXY_LOADING_DELAYED_WARNING]);
    scope.setTag(PROXIES_LOADED_COUNT_TAG, mainProxiesLength);
    scope.setTag(PROFILES_OFFSET_TAG, offset);
    scope.setTag(PROXIES_NOT_FOUND_COUNT, proxiesNotFoundCount);

    return scope;
  });
};

/*
 * Using a Promise here to perform this operation in a non-blocking manner, similar to how Sentry handles tasks.
 * Why? Even though we are only iterating through 30 items currently,
 * it is important to be cautious.
 * Page sizes may increase in the future, and slower machines could be less efficient with a blocking operation like this.
 */
export const compareProxyInProfilesWithProxyList = async (
  offset = 0,
  profilesReq: Partial<IProfilesResponse>,
  proxyList: IProxy[],
): Promise<null> => new Promise<null>((resolve) => {
  if (!profilesReq.profiles) {
    return resolve(null);
  }

  const proxiesInProfiles = profilesReq.profiles
    .filter(({ proxyEnabled, proxy }) => proxyEnabled && proxy.mode && proxy.mode !== 'none')
    .map(({ proxy }) => proxy);

  const proxiesNotFoundCount = proxiesInProfiles.reduce((acc, proxy) => {
    if (!proxy.mode || ['gologin', 'tor', 'none'].includes(proxy.mode)) {
      return acc;
    }

    // TODO: fix this any when all the interfaces get finally fixed
    const proxyFoundInList = findProxyInList(proxy as IProxy, proxyList);
    if (!proxyFoundInList) {
      acc++;
    }

    return acc;
  }, 0);

  if (proxiesNotFoundCount) {
    captureProxyLoadingDelayed({ mainProxiesLength: proxyList.length, proxiesNotFoundCount, offset });
  }

  resolve(null);
});
