import React, { FC } from 'react';

import ModernSwitch from '../../../../ui/modern-switch';
import { IChangeSetting } from '../../interfaces';
import { ContainerParams } from '../../styles';

interface ISessionSaving extends IChangeSetting{
  session: boolean;
}

export const SessionSaving: FC<ISessionSaving> = ({ changeSetting, session }) => (
  <ContainerParams>
    <ModernSwitch
      checked={session}
      onChange={(checked): void => changeSetting({ storage: { session: checked } })}
    />
  </ContainerParams>
);
