import { useSortable } from '@dnd-kit/sortable';
import { useMemo } from 'react';

import { NEW_FEATURES } from '../../../../../feature-toggle';
import { useDraggingProfilesState } from '../../../state/dragging-profiles-state.atom';

type RelativeDropPosition = 'top'|'bottom'|null;

interface ProfileSortable extends Pick<
  ReturnType<typeof useSortable>,
  'attributes'|'listeners'|'setNodeRef'|'isDragging'
> {
  relativeDropPosition: RelativeDropPosition;
}

export const useProfileSortable = (rowIdx: number): ProfileSortable => {
  const { draggingProfiles } = useDraggingProfilesState();
  const draggingProfileIdx = draggingProfiles[0]?.idx || 0;

  const {
    attributes,
    listeners,
    setNodeRef,
    isOver,
    isDragging,
  } = useSortable({ id: `profile::${rowIdx}`, disabled: !NEW_FEATURES.dragAndDrop });

  const relativeDropPosition = useMemo(() => {
    let dropPosition: 'top'|'bottom'|null = null;
    if (isOver) {
      if (draggingProfileIdx < rowIdx) {
        dropPosition = 'bottom';
      } else if (draggingProfileIdx > rowIdx) {
        dropPosition = 'top';
      }
    }

    return dropPosition;
  }, [isOver, rowIdx, draggingProfileIdx]);

  return {
    attributes,
    listeners,
    setNodeRef,
    isDragging,
    relativeDropPosition,
  };
};
