import { getProfilesList, mapAndSetProfilesList, setProfilesList } from './profiles-list.atom';
import {
  decrementProxyProfilesCount,
  getProxyList,
  incrementProxyProfilesCount,
  removeProxiesFromList,
} from './proxy-list.atom';
import { closeProxyManager, getProxyManagerState } from './proxy-manager-modal-status.atom';
import { updateSelectedProxies } from './selected-proxies.atom';
import { sendActionAnalytics } from '../features/common/api';
import { IProfileProxy } from '../features/proxy/components/interfaces/IProfileProxy';
import { IProxy } from '../features/proxy/components/interfaces/IProxy';
import { PROXY_EMPTY } from '../features/proxy/constants';
import { EMPTY_PROXY } from '../features/proxy/proxy-helpers';
import { sendPastedProxies } from '../features/proxy/send-pasted-proxies';
import { updateProfileProxy } from '../features/quickProfiles/api';

const deleteProxyFromProfiles = (proxyIds: string[]): void => {
  mapAndSetProfilesList(prevProfiles => prevProfiles.map(profile => {
    const isDeletingProfileProxy = proxyIds && proxyIds.includes(profile.proxy.id || '');
    if (!isDeletingProfileProxy) {
      return profile;
    }

    return {
      ...profile,
      proxyEnabled: false,
      proxy: PROXY_EMPTY,
      proxyType: 'none',
    };
  }));
};

export const deleteProxies = (proxyIdsToDelete: string[]): void => {
  removeProxiesFromList(proxyIdsToDelete);
  deleteProxyFromProfiles(proxyIdsToDelete);
  updateSelectedProxies({
    selectedProxies: [],
    selectedProxy: null,
  });
};

// TODO: fix `any` types for proxies
export const selectProfileProxy = async (profileId: string, proxy: IProfileProxy): Promise<void> => {
  const profilesList = getProfilesList();
  const profileInfo = profilesList.find((profile) => profile.id === profileId);
  const prevProfileProxyId = profileInfo?.proxy?.id || '';
  const noNeedToUpdate = prevProfileProxyId === (proxy._id || ' ');
  if (!profileInfo || noNeedToUpdate) {
    return;
  }

  if (proxy._id) {
    incrementProxyProfilesCount(proxy?._id);
  }

  if (prevProfileProxyId) {
    decrementProxyProfilesCount(prevProfileProxyId);
  }

  mapAndSetProfilesList(prevProfilesList => prevProfilesList.map((profile) => {
    if (profile.id !== profileId) {
      return profile;
    }

    return {
      ...profile,
      proxy: {
        id: proxy._id,
        customName: proxy.customName,
        mode: proxy.mode,
        host: proxy.host,
        port: proxy.port,
        username: proxy.username,
        password: proxy.password,
        country: proxy.country,
        city: proxy.city,
        autoProxyRegion: proxy.autoProxyRegion,
        torProxyRegion: proxy.torProxyRegion,
        profiles: (proxy as any).profiles || [],
        profilesCount: proxy.profilesCount || 0,
      } as any,
      proxyType: proxy.mode,
      proxyEnabled: proxy.mode !== 'none',
    };
  }));

  await updateProfileProxy({ ...proxy, id: profileId } as any)
    .catch(() => setProfilesList(profilesList));

  sendActionAnalytics('chose proxy via proxy manager');
};

export const unselectProfileProxy = (profileId: string): Promise<void> => selectProfileProxy(profileId, EMPTY_PROXY);

export const pasteProxies = async (profileId?: string): Promise<IProxy[]> => {
  const { currentProfileId } = getProxyManagerState();
  const proxyList = getProxyList();

  const clipboardText = await navigator.clipboard.readText();

  const proxies = await sendPastedProxies({
    clipboardText,
    profileInEditProxyMode: profileId || currentProfileId,
    proxyList,
  }) || [];

  if (proxies.length === 1 && profileId) {
    const [pastedProxy] = proxies;
    await selectProfileProxy(profileId, pastedProxy);
    closeProxyManager();
  }

  return proxies;
};
