import { atom, getDefaultStore, useAtomValue } from 'jotai';

const profilesTableEditingProfileNameAtom = atom<string | null>(null);

const useProfilesTableEditingProfileName = (): string | null => useAtomValue(profilesTableEditingProfileNameAtom);
export const useIsEditingProfileName = (profileId: string): boolean => useProfilesTableEditingProfileName() === profileId;

const getProfilesTableEditingProfileName = (): string | null => getDefaultStore().get(profilesTableEditingProfileNameAtom);
export const setProfilesTableEditingProfileName = (newEditingProfileNameId: string | null): void => getDefaultStore()
  .set(profilesTableEditingProfileNameAtom, newEditingProfileNameId);
