import React, { FC } from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';

import { useIsMoreProfilesAvailable } from '../../../state/is-more-profiles-available.atom';

interface IInfiniteScrollProfilesTable {
  dataLength: number;
  next: () => void;
  loader: JSX.Element|null;
  initialScrollY?: number;
}

export const InfiniteScrollProfilesTable: FC<IInfiniteScrollProfilesTable> = (props) => {
  const { dataLength, initialScrollY, loader, next, children } = props;

  const isMoreProfilesAvailable = useIsMoreProfilesAvailable();

  return (
    <InfiniteScroll
      dataLength={dataLength}
      next={next}
      hasMore={isMoreProfilesAvailable}
      loader={loader}
      style={{ overflow: 'visible' }}
      scrollableTarget='root'
      initialScrollY={initialScrollY}
    >
      {children}
    </InfiniteScroll>
  );
};
