import { Button } from 'antd';
import React, { FC, useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

import { AddFirstProfileTitle, FirstProfileButtonContainer } from './styles';
import { NEW_FEATURES } from '../../../../../feature-toggle';
import { ITemplatesCtx, templatesContext } from '../../../state';
import { IconPlus } from '../../../ui/gologin-header/icons';
import { IconCopy } from '../../../ui/gologin-header/icons/icon-copy';

interface INoProfilesPlaceholder {
  quickCreateProfile: (templateCtx: ITemplatesCtx) => Promise<void>;
}

const NoProfilesPlaceholder: FC<INoProfilesPlaceholder> = (props) => {
  const { quickCreateProfile } = props;

  const templateCtx = useContext(templatesContext);

  const { t: translation } = useTranslation();

  if (!NEW_FEATURES.header) {
    return (
      <div style={{ textAlign: 'center' }}>
        <Link to='/newProfile'>
          <Button type='primary'>
            {translation('newProfile.saveProfileBtnText')}
          </Button>
        </Link>
      </div>
    );
  }

  return (
    <div style={{ textAlign: 'center' }}>
      <IconCopy styleType='lightGray' padding={0} margin='0 0 20px' iconColor='var(--B5B5BA-profile-table)' />
      <AddFirstProfileTitle>
        {translation('folders.createFirstFolder.noProfile')}
      </AddFirstProfileTitle>
      <FirstProfileButtonContainer
        onClick={(): Promise<void> => quickCreateProfile(templateCtx)}
      >
        <IconPlus padding={0} styleType='lightGray' iconColor='var(--B5B5BA-profile-table)' />
        <span style={{ color: 'var(--767676-profile-table)' }}>
          {translation('folders.createFirstFolder.addProfile')}
        </span>
      </FirstProfileButtonContainer>
    </div>
  );
};

export default NoProfilesPlaceholder;
