import React, { useEffect, useRef, useState } from 'react';

import { Input } from './styles';
import { NEW_FEATURES } from '../../../../../../feature-toggle';
import { IProfile } from '../../../../interfaces';
import { randomWords } from '../../../common/randomizer';
import { updateProfileName } from '../../../drawer/api';
import PerformanceObserverService from '../../../../services/performance-observer/performance-observer.service';

interface IProfileEdit {
  profileName: string;
  profile: IProfile;
  setProfiles: (set: (prevProfiles: IProfile[]) => IProfile[]) => void;
  setChangeProfileName: (profileId: string) => void;
}

const ProfileEdit: React.FC<IProfileEdit> = ({
  profileName,
  profile,
  setProfiles,
  setChangeProfileName,
}) => {
  const [inputValue, setInputValue] = useState<string>(profileName);

  const ref = useRef<HTMLInputElement>(null);

  useEffect(() => {
    ref.current?.select();
  }, []);

  const getTrimmedOrRandomName = (name: string): string =>
    name.trim().replace(/\s{2,}/g, ' ') || randomWords();

  const saveProfileName = async (): Promise<void> => {
    const name = getTrimmedOrRandomName(inputValue);

    updateProfileName({
      id: profile.id,
      name,
    });

    setProfiles(profiles => profiles.map((profileFromTheList) => {
      if (profileFromTheList?.id === profile?.id) {
        return {
          ...profileFromTheList,
          name,
          defaultProps: {
            ...profileFromTheList?.defaultProps,
            profileNameIsDefault: false,
          },
        };
      }

      return profileFromTheList;
    }));
  };

  const handleChange: React.FormEventHandler<HTMLInputElement> = (event) => {
    event.preventDefault();
    setInputValue(event.currentTarget.value);
  };

  const handleKeyDown: React.KeyboardEventHandler<HTMLInputElement> = (
    event,
  ) => {
    if (event.key !== 'Enter') {
      return;
    }

    event.preventDefault();
    saveProfileName();
    setChangeProfileName('id');
  };

  const handleBlur: React.FormEventHandler<HTMLInputElement> = () => {
    saveProfileName();
    setChangeProfileName('id');
  };

  const handleClick: React.MouseEventHandler<HTMLInputElement> = (event) => {
    event.stopPropagation();
  };

  return (
    <Input
      id='profileNameId'
      ref={ref}
      value={inputValue}
      onChange={handleChange}
      onKeyDown={handleKeyDown}
      onBlur={handleBlur}
      onClick={handleClick}
      newStyle={!!NEW_FEATURES.header}
    />
  );
};

export default ProfileEdit;
