import React, { FC, MouseEvent } from 'react';
import { useTranslation } from 'react-i18next';

import { FooterBtn, FooterMidBtnsContainer, ProxyUpdateFormHeaderEl } from './styles';
import { closeProxyManager } from '../../../../state/proxy-manager-modal-status.atom';
import IconBack from '../../../../ui/icons/IconBack';
import IconClean from '../../../../ui/icons/IconClean';
import IconCopyProxy from '../../../../ui/icons/IconCopyProxy';
import IconPasteProxy from '../../../../ui/icons/IconPasteProxy';
import ProxyIconBtnWithTooltip from '../../components/proxy-icon-btn-with-tooltip';
import { TooltipEl } from '../../proxy-selector/styles';
import { ProxyListHeaderBtnContainer, ProxyListHeaderBtnLeft, ProxyListHeaderBtnRight } from '../proxy-list/styles/proxy-list';

interface IProps {
  clearForm: () => void;
  copyProxy: () => void;
  handlePasteProxy: () => Promise<void|true>;
}

const ProxyUpdateFormHeader: FC<IProps> = (props: IProps) => {
  const { clearForm, copyProxy, handlePasteProxy } = props;

  const { t: translation } = useTranslation();

  const handleCancelClick = (event: MouseEvent): void => {
    event.stopPropagation();
    event.preventDefault();
    closeProxyManager();
  };

  return (
    <ProxyUpdateFormHeaderEl>
      <ProxyListHeaderBtnContainer>
        <ProxyListHeaderBtnLeft type="submit">
          <div style={{ marginRight: 10 }}>
            <IconBack />
          </div>
          {translation('proxies.done')}
        </ProxyListHeaderBtnLeft>
      </ProxyListHeaderBtnContainer>

      <FooterMidBtnsContainer>
        <ProxyIconBtnWithTooltip tooltipContent={(
          <TooltipEl>
            {translation('proxies.pasteProxy')}
          </TooltipEl>
        )}>
          <FooterBtn onClick={handlePasteProxy} type="button">
            <IconPasteProxy />
          </FooterBtn>
        </ProxyIconBtnWithTooltip>

        <ProxyIconBtnWithTooltip tooltipContent={(
          <TooltipEl>
            {translation('proxies.clearForm')}
          </TooltipEl>
        )}>
          <FooterBtn onClick={clearForm} type="button">
            <IconClean />
          </FooterBtn>
        </ProxyIconBtnWithTooltip>

        <ProxyIconBtnWithTooltip tooltipContent={(
          <TooltipEl>
            {translation('proxies.copyProxy')}
          </TooltipEl>
        )}>
          <FooterBtn onClick={copyProxy} type="button">
            <IconCopyProxy />
          </FooterBtn>
        </ProxyIconBtnWithTooltip>
      </FooterMidBtnsContainer>

      <ProxyListHeaderBtnContainer>
        <ProxyListHeaderBtnRight onClick={handleCancelClick}>
          {translation('proxies.cancel')}
        </ProxyListHeaderBtnRight>
      </ProxyListHeaderBtnContainer>
    </ProxyUpdateFormHeaderEl>
  );
};

export default ProxyUpdateFormHeader;
