import styled from '@emotion/styled';

import IconArrawDown from '../../../ui/icons/IconArrawDown';

export const ReadMoreBtnEl = styled('button')`
  color: var(--767676-proxy-manager-info-error);
  outline: none;
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  letter-spacing: 0.25px;
  line-height: 20px;
`;

export const IconArrow = styled(IconArrawDown)`
  margin-left: 6px;
  stroke: var(--767676-proxy-manager-info-error);
`;

export const ErrorTextEl = styled('div')`
  padding: 3px 0 4px;
  display: flex;
  line-height: 20px;
  letter-spacing: 0.25px;
`;

export const ErrorDetailsEl = styled('div')`
  margin-top: 10px;
`;

export const ErrorDetailsListEl = styled('ul')`
  list-style: outside;
  padding: 0;
  line-height: 20px;
  letter-spacing: 0.25px;
  margin: 0 0 3px;
`;

export const ErrorDetailsListItemEl = styled('li')`
  margin-left: 20px;
`;

export const ReadMoreBlockEl = styled('div')`
  position: absolute;
  background-color: var(--FFFFFF-proxy-manager-info-error);
  border-radius: 0px 0px 4px 4px;
  box-shadow: var(--00000026-proxy-manager-info-error) 0px 2px 8px;
  top: 100%;
  left: 0;
  width: 100%;
  padding: 0px 16px 12px;
  margin-top: -13px;
  line-height: 20px;
  letter-spacing: 0.25px;

  &:after {
    content: '';
    position: absolute;
    left: 0;
    top: -5px;
    height: 5px;
    width: 100%;
    background: var(--FFFFFF-proxy-manager-info-error);
  }
`;
