import React, { FC, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { ProxyInfoRowEl } from '../styles/proxy-info';
import {
  ReadMoreBtnEl,
  IconArrow,
  ErrorDetailsEl,
  ErrorDetailsListEl,
  ErrorDetailsListItemEl,
  ErrorTextEl,
  ReadMoreBlockEl,
} from '../styles/proxy-info-error';
import { IProxy } from './interfaces/IProxy';

interface IProps {
  proxy: IProxy;
}

const ProxyInfoError: FC<IProps> = (props: IProps) => {
  const {
    proxy,
  } = props;

  const [readMore, setReadMore] = useState<boolean>(false);
  const { t: translation } = useTranslation();

  const handleReadMoreClick = (): void => {
    setReadMore(true);
  };

  const handleReadLessClick = (): void => {
    setReadMore(false);
  };

  const errorTranslation: { howToFixList: string[] } =
  {
    howToFixList: [
      translation('proxies.error.howToFixListItemOne'),
      translation('proxies.error.howToFixListItemTwo'),
      translation('proxies.error.howToFixListItemThree'),
    ],
  };

  let { howToFixList } = errorTranslation;
  if (!howToFixList) {
    howToFixList = [];
  }

  return (
    <>
      <ErrorTextEl>
        {translation('proxies.error.reason')}
      </ErrorTextEl>

      {(readMore) ? (
        <ReadMoreBlockEl>
          <ErrorDetailsEl>
            {translation('proxies.error.howToFixTitle')}
            <ErrorDetailsListEl>
              {howToFixList.map((el: string): JSX.Element => (
                <ErrorDetailsListItemEl key={`el_${el}`}>
                  {el}
                </ErrorDetailsListItemEl>
              ))}
            </ErrorDetailsListEl>
          </ErrorDetailsEl>
          <ProxyInfoRowEl>
            <ReadMoreBtnEl onClick={handleReadLessClick}>
              {translation('proxies.readLess')}
              <IconArrow color={'var(--767676-proxy-manager-info-error)'} width={16} height={10} style={{
                transform: 'scaleY(-1)',
              }} />
            </ReadMoreBtnEl>
          </ProxyInfoRowEl>
        </ReadMoreBlockEl>
      ) : (
        <ProxyInfoRowEl>
          <ReadMoreBtnEl onClick={handleReadMoreClick}>
            {translation('proxies.readMore')}
            <IconArrow color={'var(--767676-proxy-manager-info-error)'} width={16} height={10} />
          </ReadMoreBtnEl>
        </ProxyInfoRowEl>
      )}
    </>
  );
};

export default ProxyInfoError;
